import React, { useState } from 'react';
import { Modal, Button, Form, Input, Select, DatePicker } from 'antd';
import { products, gender, breed, statusFish } from '../constants';
import locale from 'antd/es/date-picker/locale/ru_RU';
import dayjs from 'dayjs';
import 'dayjs/locale/ru';

const ModalComponent = ({ handleCancel, onFinish, isModalVisible }) => {
    return (
        <div>
            <Modal
                title=" "
                open={isModalVisible}  //   `visible`  `open`
                onCancel={handleCancel}
                footer={null}
            >
                <Form
                    layout="vertical"
                    name="addElementForm"
                    onFinish={onFinish}
                >
                    <Form.Item
                        label="ID"
                        name="ID"
                        rules={[{ required: true, message: ',  ID!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="gender"
                        rules={[{ required: true, message: ',  !' }]}
                    >
                        <Select>
                            {Object.entries(gender).map(([key, value]) => (
                                <Select.Option key={key} value={value}>{value}</Select.Option>
                            ))}
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="birthDate"
                        rules={[{ required: true, message: ',   !' }]}
                    >
                        <DatePicker style={{ width: '100%' }} locale={{ locale: 'ru' }} format="DD.MM.YYYY" />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="breed"
                        rules={[{ required: true, message: ',  !' }]}
                    >
                        <Select>
                            {Object.entries(breed).map(([key, value]) => (
                                <Select.Option key={key} value={value}>{value}</Select.Option>
                            ))}
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="status"
                        rules={[{ required: true, message: ',  !' }]}
                    >
                        <Select>
                            {Object.entries(statusFish).map(([key, value]) => (
                                <Select.Option key={key} value={value}>{value}</Select.Option>
                            ))}
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="father"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="mother"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="fatherFather"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="motherFather"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="fatherMother"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="motherMother"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="line"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="  "
                        name="weight"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="bodyLength"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="bodyHeight"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="bodyThickness"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="  "
                        name="heightOfTheCaudalStem"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="   "
                        name="fultonFatnessoefficient"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=" "
                        name="thicknessIndex"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label="   "
                        name="theHeightOrLengthIndex"
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                             
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    );
};


export default ModalComponent;
