import React, { useState } from 'react';
import { Form, Input, Button, Typography, Card } from 'antd';
import 'antd/dist/reset.css';

const { Title, Text } = Typography;

const Login = ({ handleLogin, handleRegister, isRegistered, setIsRegistered, username, setUsername, password, setPassword }) => {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh', backgroundColor: '#f0f2f5' }}>
        <Card style={{ width: 400, padding: 20 }}>
          <Title level={3} style={{ textAlign: 'center' }}>{isRegistered ? '' : ''}</Title>
          <Form
            layout="vertical"
            onFinish={isRegistered ? handleLogin : handleRegister}
          >
            <Form.Item
              label=" "
              rules={[{ required: true, message: ',    !' }]}
            >
              <Input
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
            </Form.Item>
            <Form.Item
              label=""
              rules={[{ required: true, message: ',   !' }]}
            >
              <Input.Password
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" block>
                {isRegistered ? '' : ''}
              </Button>
            </Form.Item>
          </Form>
          <div style={{ textAlign: 'center' }}>
            {isRegistered ? (
              <Text>
                 ?{' '}
                <span
                  style={{ color: '#1890ff', cursor: 'pointer' }}
                  onClick={() => setIsRegistered(false)}
                >
                  
                </span>
              </Text>
            ) : (
              <Text>
                  ?{' '}
                <span
                  style={{ color: '#1890ff', cursor: 'pointer' }}
                  onClick={() => setIsRegistered(true)}
                >
                  
                </span>
              </Text>
            )}
          </div>
        </Card>
      </div>
    );
  };
export default Login ;